import {
  AttributeDefinitions,
  BirthdayFields,
  CredictCardFields,
  EmailFields,
  getTargetDriveFromProfileId,
  HomePageAttributes,
  HomePageConfig,
  HomePageFields,
  HomePageTheme,
  LinkFields,
  LocationFields,
  MinimalProfileFields,
  NicknameFields,
  PhoneFields,
} from '@youfoundation/dotyoucore-js';
import { debounce } from 'lodash-es';
import { useMemo } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import { AttributeVm } from '../../../hooks/profiles/useAttributes';
import ColorThemeSelector from '../../Form/ColorThemeSelector';
import ImageSelector from '../../Form/ImageSelector';
import Input from '../../Form/Input';
import Label from '../../Form/Label';
import Order from '../../Form/Order';
import Select from '../../Form/Select';
import Textarea from '../../Form/Textarea';

const AttributeFields = ({
  attribute,
  onChange,
}: {
  attribute: AttributeVm;
  onChange: (e: { target: { value: unknown; name: string } }) => void;
}) => {
  const debouncedChange = useMemo(() => debounce(onChange, 500), [onChange]);

  // Home consts:
  const tmpKeys = Object.keys(HomePageTheme).filter((f) => !isNaN(Number(f)));
  const tmpVals = Object.keys(HomePageTheme).filter((f) => isNaN(Number(f)));
  const DEFAULT_TABS_ORDER = ['Posts', 'Links', 'About', 'Connections'];

  switch (attribute.type) {
    case AttributeDefinitions.Name.type:
      return (
        <div className="-mx-2 flex flex-row">
          <div className="mb-5 w-2/5 px-2">
            <Label htmlFor="givenName">{t('Given name')}</Label>
            <Input
              id="givenName"
              name={MinimalProfileFields.GivenNameId}
              defaultValue={attribute.data?.[MinimalProfileFields.GivenNameId] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5 w-2/5 px-2">
            <Label htmlFor="additionalName">{t('Middle name')}</Label>
            <Input
              id="additionalName"
              name={MinimalProfileFields.AdditionalName}
              defaultValue={attribute.data?.[MinimalProfileFields.AdditionalName] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5 w-3/5 px-2">
            <Label htmlFor="surname">{t('Surname')}</Label>
            <Input
              id="surname"
              name={MinimalProfileFields.SurnameId}
              defaultValue={attribute.data?.[MinimalProfileFields.SurnameId] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </div>
      );
      break;
    case AttributeDefinitions.Nickname.type:
      return (
        <div className="mb-5 w-2/5">
          <Label htmlFor="nickName">{t('Nickname')}</Label>
          <Input
            id="nickName"
            name={NicknameFields.NickName}
            defaultValue={attribute.data?.[NicknameFields.NickName] ?? ''}
            onChange={debouncedChange}
          />
        </div>
      );
      break;
    case AttributeDefinitions.Location.type:
      return (
        <>
          <div className="mb-5">
            <Label htmlFor="Street">{t('Address')}</Label>
            <Input
              id="Street"
              name={LocationFields.Street}
              defaultValue={attribute.data?.[LocationFields.Street] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="flex-rox -mx-2 mb-5 flex">
            <div className="w-2/5 px-2">
              <Label htmlFor="Postcode">{t('Postcode')}</Label>
              <Input
                id="Postcode"
                name={LocationFields.Postcode}
                defaultValue={attribute.data?.[LocationFields.Postcode] ?? ''}
                onChange={debouncedChange}
              />
            </div>
            <div className="w-3/5 px-2">
              <Label htmlFor="City">{t('City')}</Label>
              <Input
                id="City"
                name={LocationFields.City}
                defaultValue={attribute.data?.[LocationFields.City] ?? ''}
                onChange={debouncedChange}
              />
            </div>
          </div>

          <div className="flex-rox -mx-2 mb-5 flex">
            <div className="w-1/2 px-2">
              <Label htmlFor="Locality">{t('Locality')}</Label>
              <Input
                id="Locality"
                name={LocationFields.Locality}
                defaultValue={attribute.data?.[LocationFields.Locality] ?? ''}
                onChange={debouncedChange}
              />
            </div>
            <div className="w-1/2 px-2">
              <Label htmlFor="Region">{t('Region')}</Label>
              <Input
                id="Region"
                name={LocationFields.Region}
                defaultValue={attribute.data?.[LocationFields.Region] ?? ''}
                onChange={debouncedChange}
              />
            </div>
          </div>

          <div className="mb-5">
            <Label htmlFor="Country">{t('Country')}</Label>
            <Input
              id="Country"
              name={LocationFields.Country}
              defaultValue={attribute.data?.[LocationFields.Country] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <hr className="mb-5" />
          <div className="mb-5">
            <Label htmlFor="Coordinates">{t('Coordinates')}</Label>
            <Input
              id="Coordinates"
              name={LocationFields.Coordinates}
              defaultValue={attribute.data?.[LocationFields.Coordinates] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.Birthday.type:
      return (
        <div className="-mx-2 flex flex-row">
          <div className="mb-5 w-2/5 px-2">
            <Label htmlFor="Birthday">{t('Birthday')}</Label>
            <Input
              id="Birthday"
              name={BirthdayFields.Date}
              defaultValue={attribute.data?.[BirthdayFields.Date] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </div>
      );
      break;
    case AttributeDefinitions.PhoneNumber.type:
      return (
        <div className="-mx-2 flex flex-row">
          <div className="mb-5 w-2/5 px-2">
            <Label htmlFor="Phone">{t('Phone')}</Label>
            <Input
              id="Phone"
              name={PhoneFields.PhoneNumber}
              defaultValue={attribute.data?.[PhoneFields.PhoneNumber] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </div>
      );
      break;
    case AttributeDefinitions.Email.type:
      return (
        <div className="-mx-2 flex flex-row">
          <div className="mb-5 w-2/5 px-2">
            <Label htmlFor="Email">{t('Email')}</Label>
            <Input
              id="Email"
              name={EmailFields.Email}
              defaultValue={attribute.data?.[EmailFields.Email] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </div>
      );
      break;
    case AttributeDefinitions.Photo.type:
      return (
        <div className="mb-5">
          <Label htmlFor="profileImageId">{t('Profile Image')}</Label>
          <ImageSelector
            id="profileImageId"
            name={MinimalProfileFields.ProfileImageId}
            defaultValue={attribute.data?.[MinimalProfileFields.ProfileImageId] ?? ''}
            onChange={debouncedChange}
            acl={attribute.acl}
            targetDrive={getTargetDriveFromProfileId(attribute.profileId)}
          />
        </div>
      );
      break;
    case AttributeDefinitions.InstagramUsername.type:
    case AttributeDefinitions.TiktokUsername.type:
    case AttributeDefinitions.TwitterUsername.type:
    case AttributeDefinitions.LinkedinUsername.type:
    case AttributeDefinitions.FacebookUsername.type:
      return (
        <>
          <div className="mb-5">
            <Label htmlFor="handle">
              {attribute.typeDefinition.name} {t('Username')}
            </Label>
            <Input
              id="handle"
              name={attribute.typeDefinition.name.toLowerCase()}
              defaultValue={attribute.data?.[attribute.typeDefinition.name.toLowerCase()] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.DotYouIdentity.type:
      // eslint-disable-next-line no-case-declarations
      const objectKey = attribute.typeDefinition.name.replaceAll(' ', '').toLowerCase();
      return (
        <>
          <div className="mb-5">
            <Label htmlFor="handle">{attribute.typeDefinition.name}</Label>
            <Input
              id="handle"
              name={objectKey}
              defaultValue={attribute.data?.[objectKey] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.FullBio.type:
      return (
        <>
          <div className="mb-5">
            <Label htmlFor="short-bio">{t('Bio')}</Label>
            <Input
              id="short-bio"
              name={MinimalProfileFields.ShortBioId}
              defaultValue={attribute.data?.[MinimalProfileFields.ShortBioId] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <Label htmlFor="full-bio">{t('Full bio')}</Label>
            <Textarea
              id="full-bio"
              name={MinimalProfileFields.FullBioId}
              defaultValue={attribute.data?.[MinimalProfileFields.FullBioId] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.ShortBio.type:
      return (
        <>
          <div className="mb-5">
            <Label htmlFor="short-bio">{t('Bio')}</Label>
            <Input
              id="short-bio"
              name={MinimalProfileFields.ShortBioId}
              defaultValue={attribute.data?.[MinimalProfileFields.ShortBioId] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.Link.type:
      return (
        <>
          <div className="mb-5">
            <Label htmlFor="link-text">{t('Text')}</Label>
            <Input
              id="link-text"
              name={LinkFields.LinkText}
              defaultValue={attribute.data?.[LinkFields.LinkText] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <Label htmlFor="link-target">{t('Target')}</Label>
            <Input
              id="link-target"
              name={LinkFields.LinkTarget}
              defaultValue={attribute.data?.[LinkFields.LinkTarget] ?? 'https://'}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.CreditCard.type:
      return (
        <>
          <div className="mb-5">
            <Label htmlFor="cc-alias">{t('Alias')}</Label>
            <Input
              id="cc-alias"
              name={CredictCardFields.Alias}
              defaultValue={attribute.data?.[CredictCardFields.Alias] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <Label htmlFor="cc-name">{t('Name on Card')}</Label>
            <Input
              id="cc-name"
              name={CredictCardFields.Name}
              defaultValue={attribute.data?.[CredictCardFields.Name] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <Label htmlFor="cc-number">{t('Credit card number')}</Label>
            <Input
              id="cc-number"
              name={CredictCardFields.Number}
              defaultValue={attribute.data?.[CredictCardFields.Number] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="-mx-2 mb-5 flex flex-row">
            <div className="w-1/2 px-2">
              <Label htmlFor="cc-expiration">{t('Credit card Expiration')}</Label>
              <Input
                id="cc-expiration"
                name={CredictCardFields.Expiration}
                defaultValue={attribute.data?.[CredictCardFields.Expiration] ?? ''}
                onChange={debouncedChange}
              />
            </div>
            <div className="w-1/2 px-2">
              <Label htmlFor="cc-cvc">{t('Credit card CVC')}</Label>
              <Input
                id="cc-cvc"
                name={CredictCardFields.Cvc}
                defaultValue={attribute.data?.[CredictCardFields.Cvc] ?? ''}
                onChange={debouncedChange}
              />
            </div>
          </div>
        </>
      );
      break;
    case HomePageAttributes.HomePage:
      return (
        <>
          <div className="mb-5">
            <Label htmlFor="headerImage">{t('Header Image')}</Label>
            <ImageSelector
              id="headerImage"
              name={HomePageFields.HeaderImageId}
              defaultValue={attribute.data?.[HomePageFields.HeaderImageId] ?? ''}
              onChange={debouncedChange}
              acl={attribute.acl}
              targetDrive={getTargetDriveFromProfileId(HomePageConfig.DefaultDriveId)}
            />
          </div>
          <div className="mb-5">
            <Label htmlFor="tagLine">{t('Tag Line')}</Label>
            <Input
              id="tagLine"
              name="tagLine"
              defaultValue={attribute.data?.['tagLine'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <Label htmlFor="leadText">{t('Lead Text')}</Label>
            <Textarea
              id="leadText"
              name="leadText"
              defaultValue={attribute.data?.['leadText'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case HomePageAttributes.Theme:
      return (
        <>
          <div className="mb-5">
            <Label htmlFor="themeId">{t('Theme')}</Label>
            <Select
              id="themeId"
              name="themeId"
              defaultValue={attribute.data?.['themeId'] ?? ''}
              onChange={debouncedChange}
            >
              <option value={0}>{t('Disable Public Site')}</option>
              {tmpKeys.map((tmpKey, index) => {
                return (
                  <option value={tmpKey} key={tmpKey}>
                    {t(tmpVals[index])}
                  </option>
                );
              })}
            </Select>
          </div>
          <div className="mb-5">
            <Label htmlFor="colors">{t('Color set')}</Label>
            <ColorThemeSelector
              id="colors"
              name="colors"
              defaultValue={attribute.data?.['colors'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          {(attribute.data?.['themeId'] === HomePageTheme.SocialClassic.toString() ||
            attribute.data?.['themeId'] === HomePageTheme.ContentProducer.toString()) && (
            <>
              <div className="mb-5">
                <Label htmlFor="tabs">{t('Show tabs')}</Label>
                <Select
                  id="tabs"
                  name="tabs"
                  defaultValue={attribute.data?.['tabs'] ?? ''}
                  onChange={debouncedChange}
                >
                  <option>{t('No')}</option>
                  <option value={'true'}>{t('Yes')}</option>
                </Select>
              </div>
              {attribute.data?.['tabs'] === 'true' && (
                <div className="mb-5">
                  <Label htmlFor="tabs-order">{t('Tabs Order')}</Label>
                  <Order
                    elements={
                      (attribute.data?.['tabsOrder'] &&
                        attribute.data?.['tabsOrder'].length === DEFAULT_TABS_ORDER.length &&
                        attribute.data['tabsOrder']) ??
                      DEFAULT_TABS_ORDER
                    }
                    name="tabsOrder"
                    onChange={debouncedChange}
                  />
                </div>
              )}
            </>
          )}
        </>
      );
      break;
    default:
      return (
        <>
          {Object.keys(attribute.data).map((dataKey) => {
            return (
              <p className="whitespace-pre-line" key={dataKey}>
                {dataKey}: {attribute.data[dataKey]}
              </p>
            );
          })}
        </>
      );
  }
};

export default AttributeFields;
