import { DataUtil } from '@youfoundation/dotyoucore-js';
import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { convertTextToSlug } from '../../../helpers/common';
import { t } from '../../../helpers/i18n/dictionary';
import useAttribute from '../../../hooks/profiles/useAttribute';
import useAttributeOrderer, { attributeGroup } from '../../../hooks/profiles/useAttributeOrderer';
import { AttributeVm } from '../../../hooks/profiles/useAttributes';
import Collapse from '../../ui/Icons/Colapse/Collapse';
import Plus from '../../ui/Icons/Plus/Plus';
import AttributeEditor from '../AttributeEditor/AttributeEditor';

const AttributeGroup = ({
  attributes,
  groupTitle,
  groupedAttributes,
}: {
  attributes: AttributeVm[];
  groupTitle: string;
  groupedAttributes?: attributeGroup[];
}) => {
  const { profileKey, sectionKey, typeKey } = useParams();
  const [isActive, setIsActive] = useState(
    attributes.length === 1 || convertTextToSlug(groupTitle) === typeKey
  );
  const { reorderAttr, reorderAttrGroup } = useAttributeOrderer({ attributes, groupedAttributes });
  const { mutateAsync: createNewAttr } = useAttribute({}).save;
  const navigate = useNavigate();

  const doAddAnother = async (priority?: number) => {
    const newAttr: AttributeVm = attributes?.[0]
      ? {
          profileId: attributes[0].profileId,
          sectionId: attributes[0].sectionId,
          type: attributes[0].type,
          typeDefinition: attributes[0].typeDefinition,
          acl: undefined, //{ requiredSecurityGroup: SecurityGroupType.Owner },
          id: DataUtil.getNewId(),
          priority: priority || attributes[0].priority,
          data: {
            isNew: true,
          },
        }
      : undefined;

    if (newAttr) {
      open();
      await createNewAttr(newAttr);
    }
  };

  useEffect(() => {
    if (convertTextToSlug(groupTitle) === typeKey) {
      setIsActive(true);
    } else {
      setIsActive(false);
    }
  }, [typeKey]);

  const open = () => {
    if (!isActive) {
      if (profileKey) {
        navigate(
          `/owner/profile/${profileKey}/${
            sectionKey ?? attributes[0].sectionId
          }/${convertTextToSlug(groupTitle)}`
        );
      }
      setIsActive(true);
    }
  };

  const close = () => {
    if (profileKey) {
      navigate(`/owner/profile/${profileKey}/${sectionKey ?? attributes[0].sectionId}`);
    }
    setIsActive(false);
  };

  if (attributes.length === 1) {
    return (
      <>
        <div className={`relative my-2 overflow-x-hidden`}>
          <AttributeEditor
            attribute={attributes[0]}
            reorderAttr={(_attr, dir) => reorderAttrGroup(groupTitle, dir)}
            className="mt-5 mb-2"
          />
          <AddAnotherButton onClick={() => doAddAnother(attributes[0].priority + 10)} />
        </div>
      </>
    );
  }

  return (
    <div
      className={`relative my-2 ${!isActive ? 'cursor-pointer transition-transform' : ''}`}
      style={{ paddingBottom: `${attributes.length * 10}px` }}
      onClick={open}
    >
      <span
        onClick={close}
        className={`text-slate-600 transition-opacity ${
          isActive ? 'cursor-pointer opacity-100' : 'opacity-0'
        }`}
      >
        <Collapse className={`inline-block h-4 w-4`} /> {t('Collapse')}
      </span>
      <div
        className={`mt-2 border-l-[8px] sm:border-l-[16px] ${
          isActive
            ? 'border-slate-50 dark:border-slate-800'
            : 'border-transparent opacity-50 grayscale hover:border-slate-50 hover:dark:border-slate-800'
        } transition-all ${isActive ? 'pl-2 sm:pl-5' : '-translate-x-4 hover:translate-x-0'}`}
      >
        {attributes.map((attr, index) => {
          return (
            <React.Fragment key={attr.id ?? 'pending'}>
              <span
                key={attr.id ?? 'pending'}
                title={attr.id ?? 'pending'}
                className={
                  !isActive && index !== 0
                    ? `absolute left-0 right-0 top-0 bg-white shadow-slate-50 dark:bg-slate-900`
                    : ''
                }
                style={{ transform: `translateX(${index * 4}px) translateY(${index * 10}px)` }}
              >
                <AttributeEditor
                  attribute={attr}
                  className={`${!isActive ? 'pointer-events-none my-0' : 'mt-0 mb-2'}`}
                  reorderAttr={reorderAttr}
                  title={!isActive ? `${groupTitle} (${attributes.length})` : undefined}
                />
              </span>
              {isActive && <AddAnotherButton onClick={() => doAddAnother(attr.priority + 10)} />}
            </React.Fragment>
          );
        })}
      </div>
    </div>
  );
};

const AddAnotherButton = ({ onClick }: { onClick: () => void }) => {
  return (
    <div
      className="relative mb-2 mt-[-1.6rem] flex cursor-pointer text-slate-500 transition-colors hover:text-slate-800 hover:dark:text-slate-100"
      onClick={onClick}
    >
      <span className="relative mx-auto rounded-full border bg-white transition-colors hover:bg-slate-50 dark:border-slate-600 dark:bg-slate-900 hover:dark:bg-slate-800">
        <Plus className="m-2 h-4 w-4" />
      </span>
    </div>
  );
};

export default AttributeGroup;
